import axios from "axios";
import { addDoc, collection, getDocs, query, where } from "firebase/firestore";
import { useEffect, useRef, useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import Swal from "sweetalert2";
import { insertFail, validationUrl } from "../common/common";
import { db } from "../common/fireBase";

export const Insert = (props) => {
  const [result, setResult] = useState({ url: "", who: "yuni" });
  const ip = useRef(null);

  useEffect(() => {
    const fetchIp = async () => {
      const response = await axios.get("https://geolocation-db.com/json/");
      ip.current = response.data.IPv4;
    };

    fetchIp();
  }, []);

  const inputModal = () => {
    if (result?.url === null) {
      props.onHide();
      insertFail(500);
      return false;
    }
    if (!validationUrl(result?.url)) {
      props.onHide();
      insertFail(500);
      return false;
    }
    if (blackListCheck()) {
      props.onHide();
      insertFail(401);
      return false;
    }
    const params = {
      url: result?.url,
      ip: ip.current,
      who: result?.who,
    };
    setData(params);
  };

  const setData = async (params) => {
    console.log(params);
    try {
      await addDoc(collection(db, "youtube"), params);

      const Toast = Swal.mixin({
        toast: true,
        position: "center-center",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });
      props.onHide();
      Toast.fire({
        icon: "success",
        title: `  .`,
      });
    } catch (e) {
      console.log(e);
      insertFail(400);
    }
  };

  const blackListCheck = async () => {
    // Create a query against the collection.
    try {
      let data = {};
      const docRef = query(
        collection(db, "blacklist"),
        where("ip", "==", ip.current)
      );
      const docSnap = await getDocs(docRef);
      docSnap.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        data = doc.data();
      });

      if (data && data !== {}) {
        return false;
      } else {
        return true;
      }
    } catch (e) {
      return false;
    }
  };

  const selectChange = (e) => {
    setResult((prevState) => {
      return { ...prevState, who: e.target.value };
    });
  };
  const textChange = (e) => {
    setResult((prevState) => {
      return { ...prevState, url: e.target.value };
    });
  };
  /*
    db  
    */
  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
             
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            <Form.Select onChange={selectChange}>
              <option value={"yuni"}></option>
              <option value={"kanna"}></option>
            </Form.Select>
          </p>
          <p>
            <Form.Control
              onChange={textChange}
              type="text"
              placeholder=" URL ."
            />
          </p>
              ip  .
        </Modal.Body>
        <Modal.Footer>
          <Button
            onClick={() => {
              inputModal();
            }}
          >
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
