import { useEffect, useState } from "react";
import Video from "./view/video";

import "./assets/css/App.css";
import { Insert } from "./popup/Modal";
import { Container, Nav, Navbar } from "react-bootstrap";

function App() {
  const [member, setMember] = useState("yuni");
  const [modalShow, setModalShow] = useState(false);

  return (
    <div className={"App " + member}>
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">StelLive</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => {
                member === "yuni" ? setMember("kanna") : setMember("yuni");
              }}
            >
              
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                setModalShow(true);
              }}
            >
              
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
      <Container className="text-center align-items-center">
        <Video member={member}></Video>
        <Insert show={modalShow} onHide={() => setModalShow(false)}></Insert>
      </Container>
    </div>
  );
}

export default App;
