import ReactPlayer from "react-player/lazy";
import { useEffect, useState } from "react";
import { db } from "../common/fireBase";
import { collection, getDocs, query, where } from "firebase/firestore";

const Video = ({ member }) => {
  const [playList, setPlayList] = useState([
    "https://www.youtube.com/watch?v=YJJbVAPTZLg",
  ]);

  useEffect(() => {
    readData();
  }, []);

  useEffect(() => {
    readData();
  }, [member]);

  const readData = async () => {
    try {
      const q = query(collection(db, "youtube"), where("who", "==", member));
      const querySnapshot = await getDocs(q);
      let url = [];
      querySnapshot.forEach((doc) => {
        const data = JSON.stringify(doc.data().url);
        url.push(data);
      });
      setPlayList(url.sort(() => Math.random() - 0.5));
    } catch (e) {
      console.log("dataSetError", e);
    }
  };

  return (
    <div className="wrapper">
      <ReactPlayer
        className="player"
        url={playList} // set url to the first video URL in the playlist
        muted={false}
        playing={true}
        controls={true}
        light={false}
        pip={true}
        width="100%"
        height="80%"
        //onPlay={handlePlay}
        //onPause={handlePause}
      />
    </div>
  );
};

export default Video;
