import Swal from "sweetalert2";

export const validationUrl = (value) => {
  const youtubeUrl =
    /(http:|https:)?(\/\/)?(www\.)?(youtube.com|youtu.be)\/(watch|embed)?(\?v=|\/)?(\S+)?/g;
  return youtubeUrl.test(value);
};

export const insertFail = (failCode) => {
  //failcode 400 : db 
  //500 :
  let toastText = "";
  if (failCode === 400) {
    toastText = " .   .";
  } else if (failCode === 401) {
    toastText = " IP   .";
  } else if (failCode === 500) {
    toastText = " url   .";
  } else if (failCode === 402) {
    toastText = "URL .";
  } else {
    toastText = " .";
  }
  const Toast = Swal.mixin({
    toast: true,
    position: "center-center",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
  });
  Toast.fire({
    icon: "warning",
    title: toastText,
  });
};
